//pagination default
.swiper-dot {
  text-align: center;
  
  .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      transition: 0.6s;
      background-color: $white;
      opacity: 1;
      border-radius: 10px;
      &:not(:last-child){
        margin-right: 10px;
      }
  }
  .swiper-pagination-bullet.swiper-pagination-bullet-active {
      background-color: $theme-color;
      transition: 0.6s;
      position: relative;

      &::before{
        position: absolute;
        width: 22px;
        height: 22px;
        line-height: 22px;
        top: -6px;
        left: -6px;
        border-radius: 50%;
        background-color: transparent;
        border: 2px solid $theme-color;
        content: "";
      }
   }
}

.mega-hover {
  position: relative;
  overflow: hidden;
  z-index: 2
}

.mega-hover:after,.mega-hover:before {
  content: "";
  position: absolute;
  pointer-events: none;
  opacity: 1;
  z-index: -1
}

.mega-hover:before {
  top: 0;
  right: 51%;
  bottom: 0;
  left: 50%;
  background: rgba(255,255,255,0.2)
}

.mega-hover:after {
  top: 50%;
  right: 0;
  bottom: 50%;
  left: 0;
  background: rgba(245,255,255,0.3)
}

.mega-hover:hover:before {
  left: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 900ms linear;
  transition: all 900ms linear
}

.mega-hover:hover:after {
  top: 0;
  bottom: 0;
  opacity: 0;
  -webkit-transition: all 900ms linear;
  transition: all 900ms linear
}

.array-button {

    .array-prev {
      width: 56px;
      height: 56px;
      line-height: 56px;
      text-align: center;
      background-color: transparent;
      color: $white;
      border-radius: 50%;
      @include transition;
      border: 1px solid $white;

      &:hover {
          background-color: $white;
          color: $header-color;
      }
  }

  .array-next {
      width: 56px;
      height: 56px;
      line-height: 56px;
      text-align: center;
      background-color: $theme-color-2;
      color: $white;
      border-radius: 50%;
      border: 1px solid transparent;
      @include transition;

      &:hover {
          background-color: $white;
          color: $header-color;
      }
  }
}

