//>>>>> Nice Select Css Start <<<<<//

.nice-select {
    background-color: transparent;
    border: transparent;
    float: initial;
    overflow: initial;
    height: initial;
    padding: 0;
    display: inline-flex;
    align-items: center;
    line-height: 150%;
    width: 100%;
    border: none;

    &:focus, &:hover {
      border-color: transparent;
    }

    &::after {
      height: 8px;
      width: 8px;
      right: -25px;
      top: 15px;
      border-color: $header-color;
      border-bottom: 2px solid $header-color;
      border-right: 2px solid $header-color;
    }

  .list {
    width: initial;
    background-color: $theme-color;
    box-shadow: none;
    overflow: initial;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
    width: 100%;
    top: 100%;
    padding: 0;
    max-height: 50vh;
    overflow-x: auto;
    right: -50px;
    &::-webkit-scrollbar{
      width: 2px;
      opacity: 1;
      display: block;
    }
    &::-webkit-scrollbar-button,
    &::-webkit-scrollbar-thumb{
      background: $header-color;
    }
}
.option {
  background-color: transparent;
  font-size: 16px;
  line-height: 150%;
  padding: 4px 5px;
  min-height: initial;
  font-weight: 500;
  &:hover,
  &:focus,
  &.selected.focus {
    background-color: transparent;
  }
}
.current{
  font-weight: 500;
  color: $header-color;
}
}
//>>>>> Nice Select Css End <<<<<//


.page-nav-wrap {
  ul {
      li {
          display: inline-block;
          text-align: center;

          .page-numbers {
              &.current {
                  background-color: $theme-color;
                  color: $white;
              }

              display: inline-block;
              width: 50px;
              height: 50px;
              line-height: 50px;           
              background: transparent;
              font-weight: 600;
              transition: all 0.3s ease-in-out;
              margin: 0 2px;
              border: 1px solid #E3E3E3;
              color: $header-color;

              @media (max-width: 767px) {
                  margin-top: 10px;
                  width: 50px;
                  height: 50px;
                  line-height: 50px;
                  font-size: 14px;
              }

              i {
                  margin-top: 2px;
              }

              &.icon {
                  border: none;
                  background-color: transparent;
                  color: $theme-color;

                  &:hover {
                      border: none;
                      background-color: transparent;
                      color: $theme-color;
                  }
              }

              &:hover {
                  background-color: $theme-color;
                  color: $white;
                  border: 1px solid transparent;
              }
          }
      }
  }
}


