@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");

:root {
	--body: #fff;
	--black: #000;
	--white: #fff;
	--theme: #4A136E;
	--theme2: #F55B1F;
	--header: #121315;
	--text: #666666;
	--border: #D4DCED;
	--border-2: #D4DCED;
	--bg: #1E2023;
	--box-shadow: 0px 1px 14px 0px rgba(0, 0, 0, 0.13);
}

.theme-btn {
	background-color: var(--theme);
	color: var(--white);
	display: inline-block;
	font-size: 16px;
	font-weight: 700;
	padding: 24px 50px;
	border-radius: 0;
	text-transform: uppercase;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	position: relative;
	z-index: 1;
	line-height: 1;
}

.theme-btn::before {
	content: "";
	background-color: var(--header);
	width: 0;
	height: 50%;
	position: absolute;
	top: 0;
	left: 0;
	-webkit-transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
	transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
	z-index: -1;
}

.theme-btn::after {
	content: "";
	background-color: var(--header);
	width: 0;
	height: 50%;
	position: absolute;
	bottom: 0;
	right: 0;
	-webkit-transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
	transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
	z-index: -1;
}

.theme-btn i {
	margin-left: 10px;
}

.theme-btn:hover {
	color: var(--white);
}

.theme-btn:hover::before,
.theme-btn:hover::after {
	width: 100%;
}

.theme-btn.bg-white {
	background-color: var(--white);
	color: var(--header);
}

.theme-btn.bg-white:hover {
	color: var(--white);
}

@media (max-width: 767px) {
	.theme-btn {
		padding: 20px 32px;
	}
}

@media (max-width: 575px) {
	.theme-btn {
		padding: 18px 30px;
		font-size: 14px;
	}
}

.link-btn {
	display: inline-block;
	color: var(--text);
	text-align: center;
	font-family: "DM Sans";
	font-size: 16px;
	font-weight: 600;
	text-transform: capitalize;
}

.link-btn i {
	margin-left: 10px;
	transform: rotate(-40deg);
	transition: all 0.4s ease-in-out;
}

.link-btn:hover {
	color: var(--theme);
}

.link-btn:hover i {
	margin-left: 10px;
	transform: rotate(0);
}

